@import "mixins";

h1,
h2,
h3,
h4,
h5,
h6 {
  @include sans-serif-font;
  color: rgba(255, 255, 255, .9);
  margin-top: 0%;
  margin-bottom: 0%;
}

h3 {
  // font-family: 'Open Sans', 'Helvetica Neue', Arial, sans-serif;
  @include serif-font
}

a {
  color: #FFF;
}

.skill-bar-percent {
  position:absolute;
  right:10px;
  top:0;
  font-size:12px;
  height:35px;
  line-height:35px;
  color:rgba(255, 255, 255, .9);
  visibility: hidden;
  // background);
  font-family: 'Open Sans', 'Helvetica Neue', Arial, sans-serif;
  // @include sans-serif-font;

}

.skillbar-title {
  position:absolute;
  top:0;
  left:0;
  width:10px;
  font-size:12px;
  font-weight:bold;
  // color:#ffffff;
  // border-top-left-radius:3px;
  // border-bottom-left-radius:3px;
  border-radius: 6px;
  // font-family: 'Open Sans', 'Helvetica Neue', Arial, sans-serif;
}

.skillbar-title span {
  display:block;
  width: 200px;
  padding:0 10px;
  height:35px;
  line-height:35px;
  border-top-left-radius:3px;
  border-bottom-left-radius:3px;
  color:rgba(0, 0, 0, 1);
}

.skillbar {
  position:relative;
  display:block;
  width: 100%;
  margin-bottom:15px;
  background:rgba(255, 255, 255, .9);
  height:35px;
  border-radius:6px;
}

// $navbar-padding-y: 20px;

