// }

.navfragment {
  color: rgba(0,0,0,.5);
  display: block;
  padding: .5rem 1rem;
  text-decoration: none;
  background-color: transparent;
}

.nathen {
  color: #6c757d!important;
  font-weight: 700;
}

.nathen:hover {
  color: black!important;
  transition: 0.15s linear;
}

.navlink {
  color:rgba(0,0,0,.5);
}

.navlink:hover {
  color: rgba(0, 0, 0, 1);
  transition: 0.15s linear;
}